@import url('https://fonts.googleapis.com/css?family=Pacifico|Roboto&display=swap');

body {
  font-family: 'Roboto', sans-serif;
  background-image: url(https://trevsm.github.io/race-car-bulletin/sky.jpg);
  background-repeat: repeat-y;
  background-position: center;
  background-size: cover;
}

.top {
  background-color: #3b5ba5;
  margin-bottom: 50px;
}

.top h1 {
  font-size: 32px;
  text-align: center;
  font-family: 'Pacifico', cursive;
  font-weight: 100;
  margin: 0;
  color: wheat;
  padding: 30px;
  background-color: #4257b2;
}

.top h2 {
  color: #ffffffc2;
  text-align: center;
  margin: 0;
  padding: 15px;
  font-size: 18px;
  background-color: #3b4c9b;
}

.racetrack {
  position: relative;
  margin: 0 auto;
  padding: 15px;
  width: 80%;
  min-width: 638px;
  max-width: 1500px;
  background-image: linear-gradient(
      rgba(105, 105, 105, 0.9),
      rgba(105, 105, 105, 0.9)
    ),
    url(https://trevsm.github.io/race-car-bulletin/static.gif);
  border-radius: 10px;
}

.stats {
  font-family: cursive;
  display: flex;
  font-size: 12px;
  width: 100px;
  line-height: 20px;
  vertical-align: middle;
  background-color: white;
  margin-right: 3px;
  padding-left: 2px;
  height: 20px;
  margin: 4px;
  color: #313131;
  border-radius: 20px;
}

.name {
  margin: 0 auto;
}

.score {
  font-family: monospace;
  font-weight: bold;
  border-radius: 100px;
  font-size: 13px;
  width: fit-content;
  height: 20px;
  float: right;
  line-height: 20px;
  margin-left: 10px;
  padding-right: 5px;
}

.lane {
  background-color: #404446;
}

.lane:nth-of-type(2n + 1) {
  background-color: #353738;
}

.lane {
  display: flex;
  width: 100%;
  vertical-align: middle;
}

.footer {
  background-color: #4257b2;
  height: fit-content;
  margin-top: 60px;
  padding: 10px;
  color: white;
  text-align: center;
}
.footer h3 {
  padding: 50px 0 50px 0;
  opacity: 0.3;
  font-size: 15px;
  margin: 0;
}

.track {
  width: calc(100% - 150px);
  margin: 0 auto;
  padding-right: 30px;
}

.stats {
  width: 100px;
}

.car {
  position: relative;
  height: 100%;
  z-index: 100;
}

.car:not(.start) {
  width: 0 !important;
}

.car::after {
  content: '';
  position: absolute;
  right: 0;
  top: 30%;
  transform: translate(50%, -50%) scale(0.08);
  width: 500px;
  height: 350px;
  filter: hue-rotate(264deg) brightness(4);
  background: url(data:image/png;base64,);
}

.finish-line {
  position: absolute;
  top: 0;
  right: 0;
  margin: 15px;
  width: 50px;
  bottom: 0;
  background-image: linear-gradient(45deg, black 25%, transparent 25%),
    linear-gradient(-45deg, black 25%, transparent 25%),
    linear-gradient(45deg, transparent 75%, black 75%),
    linear-gradient(-45deg, transparent 75%, black 75%);
  background-size: 20px 20px;
  background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
  background-color: white;
}

/* Loader */

.loader {
  height: 20px;
  width: 250px;

  margin: auto;
}
.loader--dot {
  animation-name: loader;
  animation-timing-function: ease-in-out;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  height: 20px;
  width: 20px;
  border-radius: 100%;
  background-color: black;
  position: absolute;
}
.loader--dot:first-child {
  background-color: #8cc759;
  animation-delay: 0.5s;
}
.loader--dot:nth-child(2) {
  background-color: #8c6daf;
  animation-delay: 0.4s;
}
.loader--dot:nth-child(3) {
  background-color: #ef5d74;
  animation-delay: 0.3s;
}
.loader--dot:nth-child(4) {
  background-color: #f9a74b;
  animation-delay: 0.2s;
}
.loader--dot:nth-child(5) {
  background-color: #60beeb;
  animation-delay: 0.1s;
}
.loader--dot:nth-child(6) {
  background-color: #fbef5a;
  animation-delay: 0s;
}
.loader--text {
  position: absolute;
  top: 200%;
  left: 0;
  right: 0;
  width: 4rem;
  margin: auto;
}
.loader--text:after {
  color: white;
  content: 'Loading';
  font-weight: bold;
  animation-name: loading-text;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}

@keyframes loader {
  15% {
    transform: translateX(0);
  }
  45% {
    transform: translateX(230px);
  }
  65% {
    transform: translateX(230px);
  }
  95% {
    transform: translateX(0);
  }
}
@keyframes loading-text {
  0% {
    content: 'Loading';
  }
  25% {
    content: 'Loading.';
  }
  50% {
    content: 'Loading..';
  }
  75% {
    content: 'Loading...';
  }
}
